body {
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  grid-template-columns: 1fr 1fr;
  justify-self: center;
  gap: 30px;
  margin: 30px 35px;
  display: grid;
}

.person {
  text-align: center;
  width: 200px;
  padding: 0;
  transition: all .3s;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.03);
}

.person__photo--wrapper {
  width: 200px;
  position: relative;
}

.person__photo {
  border-radius: 50%;
  width: 200px;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  padding: 7px 12px;
  font-size: 16px;
  font-weight: 700;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
}

.person__details {
  width: 200px;
}

@media (width >= 750px) {
  .person {
    justify-self: center;
  }

  .people {
    margin: 30px 160px;
  }

  .person {
    width: 200px;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-self: center;
    margin: 30px 305px;
    display: grid;
  }

  .person {
    width: 200px;
  }
}
